.gameboard-container {
  width: 400px;
  height: 500px;
  
  margin-top: 50px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
.title-container {
  height: 100px;
  width: 100%;
  display: flex;
  background: #1f1f1f;
  justify-content: center;
  align-items: center;
  color: white;
}
.grid-container {
  border: 1px solid grey;
}
.computer-grid {
  cursor: crosshair;
  display: flex;
  justify-content: center;
  align-items: center;
}
.computer-grid:hover {
  background: rgb(168, 168, 168);
}
.hit-empty {
  color: rgb(148, 0, 0);
  background: rgb(39, 39, 39);
}
.computer-grid i {
  
}
.hit-boat {
  color: black;
  background: white;
}
.visible-player:hover {
  background: white;
}

@media only screen and (max-width: 600px) { 
  .gameboard-container {
    width: 300px;
    height: 400px;
  }
  .gameboard-container:nth-child(3) {
    margin-top: 10px;
  }
  .grid-item {
    width: 28px;
    height: 28px;
  }
}