@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
* {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  padding: 0;
  margin: 0;
  
}
.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.gameboard-container {
  width: 400px;
  height: 500px;
  
  margin-top: 50px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
.title-container {
  height: 100px;
  width: 100%;
  display: flex;
  background: #1f1f1f;
  justify-content: center;
  align-items: center;
  color: white;
}
.grid-container {
  border: 1px solid grey;
}
.computer-grid {
  cursor: crosshair;
  display: flex;
  justify-content: center;
  align-items: center;
}
.computer-grid:hover {
  background: rgb(168, 168, 168);
}
.hit-empty {
  color: rgb(148, 0, 0);
  background: rgb(39, 39, 39);
}
.computer-grid i {
  
}
.hit-boat {
  color: black;
  background: white;
}
.visible-player:hover {
  background: white;
}

@media only screen and (max-width: 600px) { 
  .gameboard-container {
    width: 300px;
    height: 400px;
  }
  .gameboard-container:nth-child(3) {
    margin-top: 10px;
  }
  .grid-item {
    width: 28px;
    height: 28px;
  }
}
.main-container {
  flex: 1 1;
  background: #414141;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}
.grid-turn-container {
  
  background: #414141;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}
.turn {
  background: #1f1f1f;
  height: 100px;
  min-width: 200px;
  color: white;
  margin-top: 50px;
  
  display: flex;
  justify-content: center;
  align-items: center;
}


@media only screen and (max-width: 980px) {
  .turn {
    display: none;
  }
}
.end-container {
  width: 500px;
  height: 400px;
  margin-top: 50px;
  margin-bottom: 50px;
  
  background: #1f1f1f;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.end-container button {
  cursor: pointer;
  position: relative;
  border: none;
  padding: 7px;
  font-size: 20px;
  margin-top: 30px;
}


.end-container button::before {
  border: 2px solid white;
  box-sizing: border-box;
  content: ' ';
  height: 100%;
  width: 100%;
  left: 0;
  position: absolute;
  top: 0;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  transition-delay: 0s;
}
.end-container button:hover::before {
  -webkit-transform: translate3d(3px,3px,0);
          transform: translate3d(3px,3px,0);
}

@media only screen and (max-width: 600px) { 
  .end-container {
    width: 300px;
  }
}
.start-page {
  background: #414141;
  flex: 1 1;
  display: flex;
  justify-content: center;
  
}
.menu-container {
  width: 500px;
  height: 250px;
  box-shadow: 4px 4px 7px #1a1a1a;
  margin-top: 100px;
  margin-bottom: 50px;
  border-radius: 5px;
  background: rgb(11, 11, 27);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  
  grid-row-gap: 20px;
  
  row-gap: 20px;
}
.menu-container p {
  width: 100%;
  text-align: center;
}
.menu-container button {
  background: rgb(54, 54, 53);
  color: white;
  cursor: pointer;
  padding: 10px;
  border: none;
  position: relative;
}
.menu-container button::before {
  border: 2px solid rgb(54, 54, 53);
  box-sizing: border-box;
  content: ' ';
  height: 100%;
  width: 100%;
  left: 0;
  position: absolute;
  top: 0;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  transition-delay: 0s;
}
.menu-container button:hover::before {
  -webkit-transform: translate3d(3px,3px,0);
          transform: translate3d(3px,3px,0);
}
@media only screen and (max-width: 600px) { 
  .menu-container {
    width: 370px;
  }
}
.header {
  height: 130px;
  width: 100%;
  
  color: white;
  text-shadow: 3px 3px 3px #1f1f1f;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-size: 25px;
  background: #414141;
}
.header a {
  text-decoration: none;
  color: white;
}
/* Footer */

footer {
  display: flex;
  margin-bottom: auto;
  min-height: 50px;
  background: #1f1f1f;
  width: 100%;
  color: white;
  
}
footer a {
  text-decoration: none;
  color: white;
}
footer a:hover {
  color: grey;
}
footer h3 {
  margin-top: 0px;
  margin-bottom: 5px;
  margin-left: 10px;
  font-size: 15px;
}
footer i {
  font-size: 20px;
  margin-left: 10px;
  margin-bottom: 5px;
}
.left-footer {
  display: flex;

  align-items: flex-end;
  width: 50%;
  min-height: 100%;
}

.right-footer {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  width: 50%;
  min-height: 100%;
}

.right-footer i {
  margin-right: 10px;
}

.start {
  flex: 1 1;
  display: flex;
  justify-content: center;
  
  background: #414141;
}
.grid-layout-title-container {
  width: 700px;
  height: 500px;
  margin-top: 50px;
  margin-bottom: 50px;
  
  background: #1f1f1f;
  
}
.grid-layout-title-container h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 81px;
  color: white;
  text-decoration: underline;
  
}
.grid-layout-title-container p {
  text-align: center;
  color: white;
}
.grid-layout-container {
  display: flex;
  
  
}
.grid-container {
  width: 400px;
  height: 400px;
  
  display: flex;
  flex-wrap: wrap;
  
}
.grid-item {
  height: 38px;
  width: 38px;
  border: 0.2px solid grey;
  display: flex;
  justify-content: center;
  align-items: center;
}
.not-visible {
  background: rgb(39, 39, 39);
}
.visible-player {
